<template>
	<view class="main">

		<tou_hand1 handtitle='添加管理员'></tou_hand1>
		<view class="show">
			<view class="sone">
				<view class="soneleft">姓名:</view>
				<input placeholder="请输入姓名，4个字符内" v-model="name"/>
			</view>
			<view class="sone">
				<view class="soneleft">用户编号：</view>
				<input placeholder="请输入用户编号" v-model="userId" @blur="yhnumbtn" type="number"/>
			</view>
			<view class="sone">
				<view class="soneleft">真实姓名：</view>
				<view class="soneright" v-show="!Name">未设置</view>
				<view class="soneright" v-show="Name">{{Name}}</view>
			</view>
			<view class="sone">
				<view class="soneleft">手机号：</view>
				<view class="soneright" v-show="!phone1">未设置</view>
				<view class="soneright" v-show="phone1">{{phone1}}</view>
			</view>
			<view class="sone">
				<view class="soneleft">分成比例：</view>
				<input placeholder="0" v-model="proportion" type="digit" />
			</view>
			<view class="stwo">
				<view class="stwoleft">
					开通权限：
				</view>
				<view class="stworight">
					<van-checkbox-group v-model="result">
					  <van-checkbox name="a" shape="square">
						  <view class="mont">设备管理</view>
					  </van-checkbox>
					  <van-checkbox name="b" shape="square">
						  <view class="mont">运营统计</view>
					  </van-checkbox>
					  <van-checkbox name="c" shape="square">
					  	   <view class="mont">注册用户</view>
					  </van-checkbox>
					</van-checkbox-group>
				</view>
			</view>
			<view class="sthree">
				<view class="sthreehand" @click="okbtn">
					确认
				</view>
				<view class="sthreefoot">
					<view class="sthreeleft">
						提示：
					</view>
					<view class="sthreeright">
						1.分成比例上限{{num}} 请输入0-{{num}}之间的小数<br/>
						2.真实姓名请在个人资料中设置，否则影响提现
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import tou_hand1 from '../../components/tou_hand1/tou_hand1.vue'
	
	export default {
		components: {tou_hand1},
		mounted() {
			
		},

		data() {
			return {
				 result: [],
				 projectId:"",
				 name:'',//联系人
				 userId:'',//用户编号固定8位的用户编号
				 proportion:'0',//管理员分成比例
				 phone:'',//用户手机号
				 Name:'',//用户真实姓名
				 phone1:'',//用户真实手机号
				 num:''
			}
		},
		onLoad(options) {
			this.projectId=options.projectId
			this.num=options.num
		},
		onShow() {
			// this.phone=uni.getStorageSync('phone')
			// this.Name=uni.getStorageSync('Name')
			// if(!this.Name){
			// 	this.$toast.fail('用户真实姓名为空,请在“个人资料”设置，否则影响提现')
			// 	uni.navigateBack()
			// 	return
			// }
			// if(!this.phone){
			// 	this.$toast.fail('用户手机号为空,请在“个人资料”设置，否则无法登陆PC端后台')
			// 	uni.navigateBack()
			// 	return
			// }
		},
		methods: {
			
			okbtn(){
				let deviceSettings=0
				let totalSetting=0
				let userSettings=0
				for(var i=0;i<this.result.length;i++){
					if(this.result[i]=='a'){
						deviceSettings=1
					}
					if(this.result[i]=='b'){
						totalSetting=1
					}
					if(this.result[i]=='c'){
						userSettings=1
					}
				}
				if(!this.name||!this.userId||!this.proportion){
					this.$toast.fail('请完善信息')
					return
				}
				if(Number(this.proportion) >this.num){
					this.$toast.fail('分成比例超出上限，请修改后再试；')
					return
				}
				
				// var userId=String.Format("{0:D8}", parseInt(this.userId))
				function PrefixZero(num, n) {
				    return (Array(n).join(0) + num).slice(-n);
				}
				let data={
					projectId:this.projectId,
					deviceSettings:deviceSettings,
					totalSetting:totalSetting,
					userSettings:userSettings,
					name:this.name,
					userId:PrefixZero(this.userId, 8),
					mobile:this.phone,
					proportion:this.proportion,
				}
				this.$base.request1('manager/projectManager', 'POST',data)
					.then(res => {
						if(res.data.code==200){
							this.$toast.success(res.data.msg)
							
							uni.navigateBack()
						}else{
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {
				
					})
			},
			yhnumbtn(){
				if(this.userId){
					this.$base.request('user/info'+'/'+this.userId, 'GET')
						.then(res => {
							if(res.data.code==200){
								if(res.data.data.realName){
									this.Name=res.data.data.realName
								}else{
									this.Name=''
									this.$toast.fail('用户真实姓名为空,请在“个人资料”设置，否则影响提现')
									return
								}
								if(!res.data.data.phone){
									this.$toast.fail('用户手机号为空,请在“个人资料”设置，否则无法登陆PC端后台')
									return
								}else{
									this.phone1=res.data.data.phone
									this.phone=res.data.data.phone
								}
								
							}else{
								this.$toast.fail(res.data.msg)
							}
						})
						.catch(err => {
					
						})
				}else{
					this.Name=''
				}
				
			}
			
		}
	}
</script>

<style lang="scss" scoped>
	.main {
		width: 100%;
		overflow-x: hidden;
		
		.show{
			width: 100%;
			box-sizing: border-box;
			padding: 0 32upx;
			.sone{
				width: 100%;
				display: flex;
				border-bottom:1upx solid #dadada;
				padding-bottom: 27upx;
				align-items: center;
				margin-top: 27upx;
				.soneleft{
					font-size: 24upx;
					font-family: PingFang SC Regular, PingFang SC Regular-Regular;
					font-weight: 400;
					color: #333333;
					width: 37%;
				}
				input{
					font-size: 28upx;
					font-family: PingFang SC Regular, PingFang SC Regular-Regular;
					font-weight: 400;
					color: #999999;
				}
				.soneright{
					font-size: 28upx;
					font-family: PingFang SC Regular, PingFang SC Regular-Regular;
					font-weight: 400;
					color: #999999;
				}
			}
			.stwo{
				width: 100%;
				display: flex;
				border-bottom:1upx solid #dadada;
				padding-bottom: 27upx;
				align-items: center;
				margin-top: 27upx;
				.stwoleft{
					font-size: 24upx;
					font-family: PingFang SC Regular, PingFang SC Regular-Regular;
					font-weight: 400;
					color: #333333;
				}
				.stworight{
					/deep/.van-checkbox-group{
						display: flex;
						justify-content: space-between;
					}
					/deep/.van-checkbox__label {
						margin-left: 6upx;
					}
					/deep/.van-checkbox{
						margin-left: 20upx;
					}
					.mont{
						width: 120upx;
						// line-height: 40upx;
						font-size: 24upx;
						font-family: PingFang SC Medium, PingFang SC Medium-Medium;
						font-weight: 500;
						color: #333333;
					}
				}
				
			}
			.sthree{
				width: 100%;
				box-sizing: border-box;
				margin-top: 178upx;
				.sthreehand{
					width: 686upx;
					height: 88upx;
					border-radius: 8upx;
					line-height: 88upx;
					font-size: 32upx;
					font-family: PingFang SC Bold, PingFang SC Bold-Bold;
					font-weight: 700;
					text-align: center;
					color: #ffffff;
					margin: 0 auto;
					background-color: #4B98ED;
				}
				.sthreefoot{
					width: 100%;
					box-sizing: border-box;
					display: flex;
					margin-top: 32upx;
					line-height: 40upx;
					.sthreeleft{
						font-size: 24upx;
						font-family: PingFang SC Medium, PingFang SC Medium-Medium;
						font-weight: 500;
						color: #333333;
					}
					.sthreeright{
						font-size: 24upx;
						font-family: PingFang SC Medium, PingFang SC Medium-Medium;
						font-weight: 500;
						color: #666666;
					}
				}
			}
		}
		
	}
</style>
